.hero {
    width: 100%;
    position: relative;
    .container {
      align-items: center;
      margin-top: 120px;
      padding: 15px;
      height: 400px;
      max-width: 1100px;
      // border: 2px solid red;
      z-index: 1;
    }
    &-text {
      width: 50%;
      a {
        text-decoration: none;
        position: relative;
        z-index: 0;
        font-weight: 700;
        transition: 250ms all ease;
        &:before {
          z-index: -1;
          content: '';
          height: 9px;
          width: 80%;
          transition: 250ms all ease;
          background-color: darken($blue,6);
          bottom: 1px;
          right: -5%;
          position: absolute;
        }
        &:after {
          content: " →";
          top: 0;
          right: 0;
        }
        &:hover {
          color: black;
          &:before {
            right: -12%;
          }
        }
      }
    }
    &-img {
      width: 50%;
      background-image: url('../img/hero-img.png');
      background-size: contain;
      height: 400px;
      background-repeat: no-repeat;
      background-position: center right;
    }
    
}


.blob {
    position: absolute;
    top: -400px;
    right: -400px;
    width: 90vw;
    animation: moveLarge 20s ease-in-out infinite alternate;
    transform-origin: 0% 50%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
  }
  
@keyframes move {
  0%   { transform: scale(.6)   translate(50px, -370px); }
  100% { transform: scale(.8)   translate(10px, -70px) rotate(-20deg); }
}

@keyframes moveLarge {
    0%   { transform: scale(.9)   translate(10px, 0px); }
    100% { transform: scale(1)   translate(15px, 50px) rotate(-20deg); }
  }


